import uniIcon from "@/components/uni-icons/uni-icons.vue";
import myCheckboxGroup from "@/components/myCheckboxGroupPop.vue";
export default {
	components:{
		uniIcon,
		myCheckboxGroup
	},
	props:{
		title:{
			type: String,
			default: '请输入'
		},
		season:{
			type:Array
		}
		
	},
	onReady:function(){
		this.months = this.season
	},
	data() {
		return {
			months:[],
			
			items: [{
					id: 'USA',
					label: '价格太高'
				},
				{
					id: 'CHN',
					label: '布面不干净'
					
				},
				{
					id: 'BRA',
					label: '买家不满意'
				}],
			current: 0,
			bannerShow:false,
			content: ''
		};
	},
	methods:{
		tapMonths:function(index){
			let items = this.months
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				const item = items[i]
				if (item.id == index) {
					this.$set(item,'isChecked',!item.isChecked)
				}
			}
		},
		radioChange:function(evt) {
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[i].id === evt.target.id) {
					this.current = i;
					this.content = this.items[i].label
					break;
				}
			}
			
		},
		closeBanner:function(){
			this.bannerShow=false;
		},
		show:function(){
			this.bannerShow=true;
		},
		confirmBanner:function(){
			
			this.$emit('input',this.months)

			this.bannerShow = false
		}
	}
	
}
